.about__box {
  display: block;
  @media screen and (min-width: 768px) {
    display: flex;
  }
}
.cow-box {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 280px;
  height: 280px;
  border-radius: 50%;
  box-shadow: 0 4.48583px 16.8218px rgb(153 114 23 / 25%);
  background: linear-gradient(
      0deg,
      rgba(248, 210, 78, 0.7),
      rgba(248, 210, 78, 0.7)
    ),
    #d9d9d9;
  @media screen and (min-width: 768px) {
    width: 330px;
    height: 330px;
  }
  @media screen and (min-width: 1200px) {
    width: 490px;
    height: 490px;
  }
}

.about__content {
  width: 100%;
  margin-top: 40px;
  @media screen and (min-width: 768px) {
    width: 334px;
    margin-left: 38px;
  }
  @media screen and (min-width: 1200px) {
    display: block;
    width: 463px;
    margin-left: 110px;
  }
}

.about__picture {
  display: block;
  position: absolute;
  width: 100%;
  height: auto;
  margin-bottom: 40px;
  transform: translateY(40px);
}
.about__text {
  color: $secondaryColor;
  font-size: 12px;
  line-height: calc(21 / 12);
  line-height: 176%;
  margin-top: 40px;
  text-align: center;
  @media screen and (min-width: 768px) {
    text-align: left;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 32px;
    font-family: inherit;
    font-size: 14px;
    font-weight: 400;
    line-height: calc(28 / 16);
    line-height: 176%;
    letter-spacing: 0.04em;

    margin-bottom: 54px;
  }
}
.about__paragraph:not(:last-child) {
  margin-bottom: 8px;
}
.about__button {
  @extend .button;
  display: flex;
  margin: 0 auto;
  margin-top: 10px;
  font-family: inherit;
  font-weight: 600;
  font-size: 14px;
  color: $bottonBorderColor;
  background-color: $bottonDefoultColor;
  border: none;
  padding: 12px 35px;
  line-height: calc(18 / 14);
  cursor: pointer;
  transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 1200px) {
    margin-top: 54px;
    padding: 12px 35px;
  }
  &:hover {
    background-color: $bottonActiveBackgroundColor;
  }
  &:focus {
    background-color: $bottonActiveBackgroundColor;
    color: $bottonActiveColor;
    fill: $bottonActiveColor;
  }
}
.about__button-text {
  margin-right: 4px;
}
